html {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100%;
}

body {
  position: absolute;
  width: 80%;
  left: 10%;
  top: 5%;
}

.fixed-action-btn{
  position: absolute;
}

.item-form-text{
  font-size:28px;
  font-weight: bold;
}

.container{
  width: 85%;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  margin-top: 10px;
}

.add-card{
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
}

.trashcan{
  font-size: 40px;
}

.trash-col{
  text-align:right;
}

.item-check{
  margin-right: 20px;
}

.btn-padding{
  margin: 5px;
  margin-bottom: 20px;
}

.action-button{
  margin-top:4px;
}

.active .action-index{
  margin-right: 0px;
  opacity: 1;
  transition: margin-right .4s, opacity .4s ease-in;
}

.action-index{
  margin-right:-80px;
  opacity: 0;
}

.nav-wrapper{
  padding-bottom: 100px;
  margin-bottom: 20px;
}

.category{
  cursor: pointer;
  font-weight: bold;
  font-size: 28pt;
}

.home_new_list_container{
  text-align:center;
  padding: 20px;
}

.action-index{
  z-index: 9999;
}

.modal{
  width: max-content;
}

div.banner {
  padding: 20px;
  margin-top: 60px;
}

form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5 {
  margin: 20px 0;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #ec407a;
  box-shadow: none;
}

input[type=text]:not(.browser-default):focus:not([readonly]) + label,
textarea.materialize-textarea:focus:not([readonly]) + label,
input[type=email]:not(.browser-default):focus:not([readonly]) + label,
input[type=password]:not(.browser-default):focus:not([readonly]) + label{
  color: #ec407a !important;
}
